    font-weight: 600;
    color: #151515;
    margin: 0 0 24px;
    padding-right: 30px; }
    .upload__title span {
      display: block;
      font-size: 16px; }
      .upload__title span:first-of-type {
        margin-top: 10px; }
  .upload__desc {
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
    color: #94a0b1;
    margin: 0; }
  .upload__result {
    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    color: #444444;
    display: none;
    position: relative;
