    background-color: #f4f4f8;
    align-items: center;
    margin-top: 8px; }
    .upload__result:first-child {
      margin-top: 0; }
    .upload__result:nth-child(2) {
      margin-top: 16px; }
    .upload__result.show {
      display: flex; }
    .upload__result.loader {
      color: #94a0b1; }
    .upload__result.loader::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 22px;
      width: 20px;
